    created(){
        this.userName = localStorage.getItem("username")
    },
    methods:{
        logout(){
            this.isShow = true;
        },
        zdd(){
            this.isShow = false;
        },
        logout1(){
            this.isShow = false;
            localStorage.removeItem("username");
            this.$router.push("/profile")
